.selection {
  overflow: hidden;
}

.selection__form {
  margin-top: 27px;

  @media (min-width: $tablet-width) {
    margin-top: 53px;
    padding-left: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 66px;
    padding-left: 0;
  }
}

.selection__heading {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 36px;
  line-height: 36px;
  color: $black;
  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 56px;
  }
}

.selection__text {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $black;
  width: 226px;
  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    width: 50%;
    margin-bottom: 66px;
  }

  @media (min-width: $desktop-width) {
    width: 27%;
  }
}

.selection__wrapper {
  @media (min-width: $tablet-width) {
    width: 94%;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
    width: 100%;
  }
}
